@use 'sass:map';
@use '../abstracts/variables';
@use '../abstracts/functions';
@use '../abstracts/mixins';

.wrapper {
	position: relative;
	min-width: variables.$defaultMinWidth;
	overflow: hidden;
	min-height: 100vh;

	.container_wrapper {
		min-height: calc(100vh - 80px);

		.breadcrumb_wrap {
			padding-top: 20px;

			.breadcrumb_inner {
				@include mixins.inner_width;

				.breadcrumb_list {
					font-size: 0;

					.breadcrumb_item {
						position: relative;
						display: inline-block;
						vertical-align: top;

						.home_link {
							display: block;
							width: 14px;
							height: 19px;
							background: {
								position: center;
								repeat: no-repeat;
							}
							@include mixins.ratio_images('../images/ico_breadcrumb_home', 'png', 14px, 13px);
						}

						.btn_sub_depth {
							position: relative;
							padding-bottom: 8px;
							@include mixins.font_size_xxs;
							color: variables.$textColorLightGray;
							font-weight: variables.$fontWeightMedium;

							&:after {
								content: '';
								display: inline-block;
								margin: 1px 0 0 3px;
								width: 16px;
								height: 16px;
								background: {
									position: center;
									repeat: no-repeat;
								}
								@include mixins.ratio_images('../images/ico_breadcrumb_more', 'png', 16px, 16px);
								vertical-align: top;
								@include mixins.transition(background-image .23s);
							}

							.sub_depth_grid {
								position: absolute;
								top: 27px;
								left: 0;
								display: none;
								width: 125px;
								padding: 15px 0;
								background-color: #fff;
								border: 1px solid variables.$colorGray03;
								border-radius: 10px;
								box-sizing: border-box;
								z-index: 10;

								.sub_depth_box {
									position: relative;
									width: 125px;
									min-width: 125px;
									padding: 0 12px 0 15px;
									box-sizing: border-box;

									.sub_depth_list {
										.depth_item {
											.depth_link {
												@include mixins.font_size_xxs;
												color: variables.$textColorLightGray;
											}

											&.active {
												.depth_link {
													color: variables.$textColorBlack;
													font-weight: variables.$fontWeightMedium;
												}
											}

											+ .depth_item {
												margin-top: 8px;
											}
										}
									}

									&:after {
										content: '';
										position: absolute;
										top: 0;
										bottom: 0;
										right: 0;
										width: 1px;
										background-color: variables.$colorGray04;
									}

									&:last-child {
										&:after {
											content: none;
										}
									}
								}

								&.col2 {
									width: 251px;
								}

								&.col3 {
									width: 377px;
								}
							}
						}

						.sub_depth_grid {
							position: absolute;
							top: 27px;
							left: 0;
							display: none;
							width: 125px;
							padding: 15px 0;
							background-color: #fff;
							border: 1px solid variables.$colorGray03;
							border-radius: 10px;
							box-sizing: border-box;
							z-index: 10;

							.sub_depth_box {
								position: relative;
								width: 125px;
								min-width: 125px;
								padding: 0 12px 0 15px;
								box-sizing: border-box;

								.sub_depth_list {
									.depth_item {
										.depth_link {
											@include mixins.font_size_xxs;
											color: variables.$textColorLightGray;
										}

										&.active {
											.depth_link {
												color: variables.$textColorBlack;
												font-weight: variables.$fontWeightMedium;
											}
										}

										+ .depth_item {
											margin-top: 8px;
										}
									}
								}

								&:after {
									content: '';
									position: absolute;
									top: 0;
									bottom: 0;
									right: 0;
									width: 1px;
									background-color: variables.$colorGray04;
								}

								&:last-child {
									&:after {
										content: none;
									}
								}
							}

							&.col2 {
								width: 251px;
							}

							&.col3 {
								width: 377px;
							}
						}

						&.active {
							.btn_sub_depth {
								&:after {
									@include mixins.ratio_images('../images/ico_breadcrumb_more_active', 'png', 16px, 16px);
								}
							}

							.sub_depth_grid {
								@include mixins.justify_width;
								opacity: 0;
								@include mixins.transition(opacity .23s);
							}

							&.animated {
								.sub_depth_grid {
									opacity: 1;
								}
							}
						}

						~ .breadcrumb_item {
							margin-left: 10px;
						}
					}
				}
			}

			~ .contents_wrap {
				padding-top: 12px;
			}
		}

		.contents_wrap {
			padding-top: 60px;

			.contents_inner {
				width: 1200px;
				margin: 0 auto;
			}

			&.aside {
				.contents_inner {
					@include mixins.justify_width;

					.aside_wrap {
						width: 220px;
						padding-right: 60px;
					}

					.section_wrap {
						width: calc(100% - 220px);
					}
				}
			}
		}
	}

	.fly_menu_wrapper {
		position: absolute;
		display: block;
		width: 100px;
		top: 268px;
		right: 10px;
		z-index: 11;

		&.sps-blw {
			position: fixed;
			min-width: auto;
			width: 100px;
			top: 140px;
			right: 10px;
			@include mixins.transform(translateX(0));
		}

		.fly_menu_inner {
			overflow: hidden;
			border: 1px solid variables.$colorGray03;
			border-radius: 10px;
			padding: 7px 7px 16px;
			background-color: #fff;

			.fly_menu_banner {
				display: block;
				margin-bottom: 16px;
				border-radius: 6px;
				overflow: hidden;

				img {
					width: 100%;
				}
			}

			.fly_menu_link {
				position: relative;
				display: block;
				padding: 0 10px 0 2px;
				@include mixins.font_size_min;
				color: variables.$textColorGray;

				&:after {
					content: '';
					position: absolute;
					right: 2px;
					top: 50%;
					display: inline-block;
					width: 7px;
					height: 7px;
					@include mixins.ratio_images('../images/ico_fold_plus', 'png', 7px, 7px);
					vertical-align: top;
					@include mixins.transform(translateY(-50%));
				}
			}

			.fly_event_area {
				.fly_menu_link {
					margin-bottom: 11px;
				}

				.fly_event_banner {
					.event_link {
						display: block;
						border-radius: 6px;
						overflow: hidden;

						img {
							width: 100%;
						}
					}

					.swiper_control_box {
						margin-top: 8px;

						.swiper-pagination {
							&.swiper-pagination-fraction {
								padding: 0 8px;
							}
						}
					}
				}
			}

			.fly_link_box {
				margin-top: 20px;

				.fly_menu_link {
					~ .fly_menu_link {
						margin-top: 17px;
					}
				}
			}
		}

		.btn_shopping_guide {
			margin-top: 4px;
			display: block;
			width: 100%;
			height: 45px;
			padding: 13px 0 15px;
			border-radius: 10px;
			background-color: variables.$kyoboPurple;
			text-align: center;

			.ico_shopping {
				display: inline-block;
				width: 16px;
				height: 16px;
				@include mixins.ratio_images('../images/ico_shopping_guide', 'png', 16px, 16px);
				margin-right: 4px;
				vertical-align: top;
			}

			.text {
				position: relative;
				@include mixins.font_size_min;
				color: #fff;
				font-weight: variables.$fontWeightBold;
			}
		}
	}

	.floating_wrapper {
		position: fixed;
		display: block;
		width: 100px;
		right: 10px;
		bottom: 50px;
		z-index: 100;
		background-color: transparent;

		.btn_go_top {
			display: block;
			margin: 0 auto;
			width: 50px;
			height: 50px;
			padding-top: 14px;
			border-radius: 100%;
			background-color: #fff;
			border: 1px solid variables.$colorGray02;
			font-weight: variables.$fontWeightMedium;
			text-align: center;
			@include mixins.font_size_min;
			color: variables.$textColorGray;
			box-shadow: 0 5px 10px 0 rgba(variables.$textColorBlack, 0.1);
			box-sizing: border-box;

			&:before {
				content: '';
				display: block;
				width: 9px;
				height: 5px;
				background: {
					position: center;
					repeat: no-repeat;
				}
				@include mixins.ratio_images('../images/arw_go_top', 'png', 9px, 5px);
				margin: 0 auto 1px;
			}
		}

		.btn_history {
			position: relative;
			display: block;
			width: 50px;
			height: 50px;
			margin: 0 auto 16px;
			border: 1px solid variables.$textColorBlack;
			border-radius: 100%;
			overflow: visible;

			.img_box {
				position: relative;
				overflow: hidden;
				width: 100%;
				height: 100%;
				border-radius: 100%;
				box-sizing: border-box;

				img {
					display: block;
					width: 100%;
				}
			}

			.count {
				position: absolute;
				top: -3px;
				right: -3px;
				display: block;
				min-width: 20px;
				height: 20px;
				padding: 2px 2px 0 3px;
				border-radius: 100%;
				background-color: variables.$kyoboPurple;
				font-size: 11px;
				font-weight: variables.$fontWeightMedium;
				color: #fff;
				text-align: center;
				box-sizing: border-box;
			}
		}
	}

	&.contents_detail_view {
		.floating_wrapper {
			bottom: 144px;
		}
	}
}

