@use 'sass:map';
@use '../abstracts/variables';
@use '../abstracts/functions';
@use '../abstracts/mixins';

.wrapper {

	&.contents_detail_view {
		.footer_wrapper {
			margin-bottom: 95px;
		}
	}

	.footer_wrapper {
		margin-top: 150px;

		.quick_link_area {
			background-color: variables.$colorGray06;

			.quick_link_inner {
				@include mixins.inner_width;
				padding: 40px 24px 39px;
				box-sizing: border-box;

				.quick_link_list {
					@include mixins.justify_width(start, between);

					.quick_link_item {
						display: block;
						text-align: center;

						.quick_link {
							display: block;
							height: 100%;
							@include mixins.font_size_xs;

							&:before {
								content: '';
								display: block;
								width: 80px;
								height: 80px;
								margin: 0 auto 11px;
								background-repeat: no-repeat;
								background-size: 100%;
							}
						}

						&.mobile {
							.quick_link {
								&:before {
									@include mixins.ratio_images('../images/ico_footer_mobile', 'png', 80px, 80px);
								}
							}
						}

						&.quick_delivery {
							.quick_link {
								&:before {
									@include mixins.ratio_images('../images/ico_footer_quick_delivery', 'png', 80px, 80px);
								}
							}
						}

						&.compare {
							.quick_link {
								&:before {
									@include mixins.ratio_images('../images/ico_footer_compare', 'png', 80px, 80px);
								}
							}
						}

						&.giftcard {
							.quick_link {
								&:before {
									@include mixins.ratio_images('../images/ico_footer_giftcard', 'png', 80px, 80px);
								}
							}
						}

						&.store_guide {
							.quick_link {
								&:before {
									@include mixins.ratio_images('../images/ico_footer_storeguide', 'png', 80px, 80px);
								}
							}
						}

						&.sitemap {
							.quick_link {
								&:before {
									@include mixins.ratio_images('../images/ico_footer_sitemap', 'png', 80px, 80px);
								}
							}
						}

					}
				}
			}
		}

		.footer_inner {
			@include mixins.inner_width;
		}

		.footer_header {
			border-top: 1px solid variables.$colorGray03;

			.footer_inner {
				padding: 8px 0 9px;
				@include mixins.justify_width(center);
			}

			.notice_wrap {
				@include mixins.justify_width(center);
				overflow: hidden;
				margin-top: 1px;

				.notice_text {
					flex-shrink: 0;
					display: inline-block;
					@include mixins.font_size_xs;
					font-weight: variables.$fontWeightBold;
					vertical-align: top;
				}

				.notice_link {
					overflow: hidden;
					display: inline-block;
					width: 260px;
					margin-left: 20px;
					vertical-align: top;
					@include mixins.ellipsis;
					@include mixins.font_size_xs;
					text-decoration: underline;
				}

				&.winner {
					margin-left: 70px;

					.notice_link {
						width: 333px;
					}
				}
			}

			.family_site_box {
				position: relative;
				width: 180px;
				margin-left: 70px;

				.btn_family_site {
					position: relative;
					width: 180px;
					height: 44px;
					padding: 0 22px;
					background-color: variables.$colorGray06;
					border: 1px solid variables.$colorGray06;
					border-radius: 22px;
					text-align: left;
					box-sizing: border-box;
					@include mixins.transition(background-color .23s, border-color .23s);

					.text {
						@include mixins.font_size_xxs;
					}

					&:after {
						content: '';
						position: absolute;
						right: 18px;
						top: 50%;
						display: block;
						width: 8px;
						height: 4px;
						margin-top: -2px;
						background: {
							position: center;
							repeat: no-repeat;
						}
						@include mixins.ratio_images('../images/arw_familysite_more', 'png', 8px, 4px);
						@include mixins.transition(transform .23s);
					}
				}

				.family_site_list {
					display: none;
					position: absolute;
					left: 0;
					bottom: -20px;
					width: 180px;
					padding: 20px 22px;
					border: 1px solid variables.$colorGray03;
					border-radius: 20px;
					background-color: #fff;
					box-sizing: border-box;
					@include mixins.transition(opacity .23s, bottom .23s);

					.family_site_item {
						display: block;

						.family_site_link {
							display: block;
							@include mixins.font_size_xxs;
							color: variables.$textColorGray;
							text-decoration: none;
							@include mixins.transition(all .2s);

							&:hover {
								color: variables.$textColorBlack;
								text-decoration: underline;
							}
						}

						+ .family_site_item {
							margin-top: 5px;
						}
					}

				}

				&.active {
					.family_site_list {
						display: block;
						opacity: 0;
					}

					&.animated {
						.btn_family_site {
							background-color: #fff;
							border-color: variables.$textColorBlack;

							&:after {
								@include mixins.transform(rotate(180deg));
							}
						}

						.family_site_list {
							opacity: 1;
							display: block;
							bottom: 48px;
						}
					}
				}
			}

			.sns_share_box {
				margin-left: 20px;
				font-size: 0;

				.btn_sns_share {
					display: inline-block;
					width: 32px;
					height: 32px;
					vertical-align: top;
					background: {
						position: center;
						repeat: no-repeat;
					}

					&.youtube {
						@include mixins.ratio_images('../images/btn_footer_sns_youtube', 'png', 32px, 32px);
					}

					&.fb {
						@include mixins.ratio_images('../images/btn_footer_sns_fb', 'png', 32px, 32px);
					}

					&.insta {
						@include mixins.ratio_images('../images/btn_footer_sns_insta', 'png', 32px, 32px);
					}

					+ .btn_sns_share {
						margin-left: 8px;
					}
				}
			}
		}

		.footer_body {
			padding: 40px 0 51px;
			border-top: 1px solid variables.$colorGray03;

			.logo_box {
				width: 134px;
				height: 42px;

				.logo_link {
					display: block;

					img {
						width: 100%;
					}
				}
			}

			.footer_contents_wrap {
				margin-top: 28px;
				@include mixins.justify_width;

				.btn_footer_link {
					display: inline-block;
					padding: 5px 5px;
					border: 1px solid variables.$colorGray02;
					border-radius: 4px;
					@include mixins.font_size_min;
					font-weight: variables.$fontWeightMedium;
					line-height: 1;
					vertical-align: top;

					&:after {
						content: '';
						display: inline-block;
						width: 6px;
						height: 6px;
						margin: 3px 0 0 2px;
						background: {
							position: center;
							repeat: no-repeat;
						}
						@include mixins.ratio_images('../images/arw_footer_link', 'png', 6px, 6px);
						vertical-align: top;
					}
				}

				.footer_contents_left {
					flex: 1;

					.footer_menu_box {
						.footer_menu_list {
							font-size: 0;

							.footer_menu_item {
								display: inline-block;
								vertical-align: top;

								.footer_menu_link {
									@include mixins.font_size_xs;
									font-weight: variables.$fontWeightMedium;
									color: variables.$textColorGray;
								}

								+ .footer_menu_item {
									&:before {
										content: '';
										display: inline-block;
										width: 1px;
										height: 12px;
										margin: 4px 12px 0;
										background-color: variables.$colorGray03;
										vertical-align: top;
									}
								}

								&.privacy {
									.footer_menu_link {
										font-weight: variables.$fontWeightBold;
										color: variables.$textColorBlack;
										text-decoration: underline;
									}
								}
							}
						}
					}

					.footer_info_box {
						margin-top: 20px;
						font-size: 0;

						.info_text {
							display: inline-block;
							margin-top: 4px;
							@include mixins.font_size_xxs;
							color: variables.$textColorGray;
							vertical-align: top;

							+ .btn_footer_link {
								margin-left: 4px;
								@include mixins.transform(translateY(1px));
							}

							&.call {
								font-weight: variables.$fontWeightBold;
							}
						}

						.gap {
							content: '';
							display: inline-block;
							width: 1px;
							height: 9px;
							margin: 8px 6px 0;
							background-color: variables.$colorGray03;
							vertical-align: top;
						}
					}

					.copyright {
						margin-top: 21px;
						@include mixins.font_size_min;
						color: variables.$textColorLightGray;
					}
				}

				.footer_contents_right {
					margin-left: auto;

					.footer_service {
						font-size: 0;

						.service_text {
							display: inline-block;
							margin: 3px 6px 0 0;
							@include mixins.font_size_min;
							color: variables.$textColorGray;
							vertical-align: top;
						}

						.service_desc {
							margin-top: 3px;
							@include mixins.font_size_min;
							color: variables.$textColorLightGray;
						}
					}

					.footer_mark_isms {
						@include mixins.justify_width(center);
						margin-top: 22px;

						.mark_link {
							display: block;
							@include mixins.font_size_min;
							color: variables.$textColorLightGray;
							padding: 7px 0 7px 54px;
							background: {
								position: 0 0;
								repeat: no-repeat;
							}
							@include mixins.ratio_images('../images/img_mark_isms', 'png', 48px, 48px);

						}

						.mark_desc {
							display: block;
							margin-left: 17px;
							@include mixins.font_size_min;
							color: variables.$textColorLightGray;
						}
					}
				}
			}
		}
	}
}