@use 'sass:map';
@use '../abstracts/variables';
@use '../abstracts/functions';
@use '../abstracts/mixins';

.wrapper {
	.container_wrapper {
		.contents_wrap {
			.contents_inner {
				.error_wrap {
					@include mixins.justify_width(center, center, column);
					text-align: center;
					padding-top: 90px;
					min-height: calc(100vh - 268px - 90px);

					&:before {
						content: '';
						width: 186px;
						height: 140px;
						background: #ccc;
						background-size: 100%;
						//border: 1px solid #595959; //임시
						margin-bottom: 40px;
						box-sizing: border-box;
					}

					.error_text_wrap {
						letter-spacing: variables.$letterSpacing;
						font-size: variables.$fontSizeDef;
						color: variables.$textColorGray;
						line-height: 24px;

						.error_text_bold {
							display: block;
							font-size: variables.$fontSizeMd;
							color: variables.$textColorBlack;
							font-weight: variables.$fontWeightBold;
							line-height: 30px;
							margin-bottom: 16px;

						}

					}

					.btn_wrap {
						margin-top: 60px;
					}
				}
			}
		}
	}
}