@use 'sass:map';
@use '../abstracts/variables';
@use '../abstracts/functions';
@use '../abstracts/mixins';

/* 통합검색 */
.auto_complete_wrap {
	.search_content_wrap {
		display: block;
		box-sizing: border-box;

		// 데이터 없는 경우
		.no_data_box {
			display: block;
			box-sizing: border-box;

			.no_data_text {
				display: block;
				position: relative;
				color: variables.$textColorLightGray;
				@include mixins.font_size_xxs;
			}
		}

		// 검색어 입력 전 - 상단 최근 검색어
		.recommend_keyword_area {
			padding-bottom: 30px;
			margin: 0 10px 30px 10px;
			border-bottom: 1px solid variables.$colorGray04;
			box-sizing: border-box;
		}

		// 검색어 입력 전 - 하단 컨텐츠
		.keyword_contents_area {
			@include mixins.justify_width;
			align-items: stretch;
			margin: 0 10px;
			box-sizing: border-box;

			// 지금 많이 찾는 상품 or 함께 많이 본 상품
			.keyword_prod_box {
				flex: 0 auto;
				width: 310px;
				margin: 0 42px;

				.title_wrap {
					margin: 0 10px;
				}

				.keyword_prod_swiper_wrap {
					width: 100%;

					.keyword_prod_swiper {
						width: 100%;
					}
				}
			}

			// 실시간 인기 검색어
			.hot_keyword_box {
				//@include mixins.justify_width(start, start, column);
				//align-items: stretch;
				flex: 0 auto;
				width: 280px;

				.hot_keyword_list_wrap {
					@include mixins.justify_width(start, start, column);
					align-items: stretch;
					flex: 1;

					.hot_keyword_list {
						display: block;
						margin-bottom: 16px;

						.hot_keyword_item {
							display: block;

							@include mixins.clearfix;

							.left_area {
								float: left;
								font-size: 0;
								width: calc(100% - 32px);

								.hot_keyword_link {
									display: inline-block;
									max-width: 100%;

									.rank {
										display: inline-block;
										flex: 0 auto;
										width: 15px;
										margin-right: 10px;
										color: variables.$textColorGray;
										@include mixins.font_size_xxs;
										font-weight: variables.$fontWeightBold;
										vertical-align: top;
									}

									.keyword {
										display: inline-block;
										width: calc(100% - 25px);
										flex: 1;
										color: variables.$textColorBlack;
										@include mixins.font_size_xxs;
										@include mixins.ellipsis(false, true, false);
										vertical-align: top;
									}

									&:hover, &:focus, &:active {
										.keyword {
											text-decoration: underline;
										}
									}
								}
							}

							.right_area {
								float: right;

								.rank_status {
									display: block;
									width: 9px;
									height: 19px;
									background: {
										repeat: no-repeat;
									}
									@include mixins.ratio_images('../images/ico_rank_status', 'png', 9px, 19px);
									vertical-align: top;

									&.up {
										@include mixins.ratio_images('../images/ico_rank_status_up', 'png', 9px, 19px);
									}

									&.down {
										@include mixins.ratio_images('../images/ico_rank_status_down', 'png', 9px, 19px);
									}
								}
							}

							& ~ .hot_keyword_item {
								margin-top: 16px;
							}

							&.top {
								.left_area {
									.rank {
										color: variables.$textColorGreen;
									}
								}
							}
						}
					}

					.hot_keyword_bottom {
						margin-top: auto;

						.info_text {
							text-align: right;
						}
					}
				}
			}

		}

		// 검색어 입력 시 - col box
		.col_left_box {
			flex-shrink: 0;
			width: 490px;
			box-sizing: border-box;
		}

		.col_right_box {
			flex-shrink: 0;
			width: 430px;
			margin-left: 34px;
			padding-right: 10px;
			box-sizing: border-box;
		}

		// 검색어 입력 시 - 상품
		.related_product_area {
			@include mixins.justify_width;
			align-items: stretch;
			box-sizing: border-box;

			.col_left_box {
				.title_wrap {
					margin: 0 10px;

					.control_related_prod {
						margin-right: 10px;
					}
				}
			}

			& ~ .related_contents_area {
				margin-top: 40px;
			}

			.related_prod_swiper_wrap {

			}

			.related_prod_detail_wrap {
				display: block;
				padding-top: 41px;

				.prod_summary_box {
					display: block;
					padding: 19px 19px;
					margin-top: 20px;
					border: 1px solid variables.$colorGray04;
					border-radius: 10px;
					box-sizing: border-box;

					.info_text {
						color: variables.$textColorLightGray;
						@include mixins.multi_ellipsis(variables.$fontSizeXxsLineHeight, 5, false);
					}
				}
			}
		}

		// 검색어 입력 시 - 하단 컨텐츠
		.related_contents_area {
			@include mixins.justify_width;
			align-items: stretch;
			box-sizing: border-box;

			.col_left_box {
				padding-left: 10px;
			}

			// 작가
			.related_author_list_wrap {
				.related_author_list {
					.related_author_item {
						display: block;
						color: variables.$textColorBlack;
						@include mixins.font_size_xxs;

						.related_author_link {
							@include mixins.justify_width(start, start, row, true);

							&:hover, &:focus, &:active {
								text-decoration: underline;
							}

							.author {
								position: relative;
								flex: 0 0 auto;
								margin-right: 3px;
							}

							.text {
								position: relative;
								flex: 1 0 0;
								@include mixins.multi_ellipsis(variables.$fontSizeXxsLineHeight, 2, false);

								.matched {
									font-weight: variables.$fontWeightBold;
								}
							}
						}

						& ~ .related_author_item {
							margin-top: 16px;
						}
					}
				}
			}

			// 이벤트
			.related_event_swiper_wrap {
				.related_event_swiper {
					.swiper-slide {
						width: 200px;
						margin-right: 20px;

						&:last-child {
							margin-right: 0;
						}
					}

					.related_event_wrap {
						display: block;
						width: 200px;
						box-sizing: border-box;

						.related_event_link {
							display: block;
							width: 100%;

							.event_img_box {
								display: block;
								width: 100%;
								overflow: hidden;
								border-radius: 10px;

								img {
									width: 100%;
								}
							}

							.event_info_box {
								display: block;
								margin-top: 12px;

								.event_title {
									display: block;
									color: variables.$textColorBlack;
									@include mixins.font_size_xxs;
									font-weight: variables.$fontWeightMedium;
								}

								.event_period {
									display: block;
									margin-top: 5px;
									color: variables.$textColorLightGray;
									@include mixins.font_size_min;
								}
							}
						}
					}
				}
			}
		}
	}
}

// 최근 검색어
.recent_keyword_box {
	@include mixins.justify_width(start, start, column);
	align-items: stretch;
	flex: 0 auto;
	width: 260px;

	.recent_keyword_list_wrap {
		@include mixins.justify_width(start, start, column);
		align-items: stretch;
		flex: 1;

		.recent_keyword_list {
			display: block;
			margin-bottom: 15px;

			.recent_keyword_item {
				@include mixins.justify_width(center);

				.left_area {
					flex: 1;
					font-size: 0;

					.recent_keyword_link {
						display: inline-block;
						max-width: 184px;

						.keyword {
							display: inline-block;
							color: variables.$textColorBlack;
							@include mixins.font_size_xxs;
							@include mixins.ellipsis(false, true, false);
						}

						&:hover, &:focus, &:active {
							.keyword {
								text-decoration: underline;
							}
						}
					}
				}

				.right_area {
					margin-left: auto;
					font-size: 0;

					.date {
						display: inline-block;
						color: variables.$textColorLightGray;
						@include mixins.font_size_min;
						vertical-align: top;
					}

					.btn_keyword_del {
						display: inline-block;
						width: 15px;
						height: 15px;
						margin: 1px 0 0 8px;
						background: {
							position: center;
							repeat: no-repeat;
						}
						@include mixins.ratio_images('../images/btn_delete', 'png', 15px, 15px);
						vertical-align: top;
					}
				}

				& ~ .recent_keyword_item {
					margin-top: 16px;
				}
			}
		}

		.no_data_box {
			@include mixins.justify_width(center, center, column);
			width: 100%;
			height: 333px;
			text-align: center;

			.no_data_text {
				padding-top: 40px;

				&:before {
					content: '';
					display: inline-block;
					width: 30px;
					height: 30px;
					@include mixins.ratio_images('../images/ico_exclamation_gray', 'png', 30px, 30px);
					position: absolute;
					top: 0;
					left: 50%;
					margin-left: -15px;
				}
			}
		}

		.recent_keyword_bottom {
			margin-top: auto;

			.btn_wrap {
				text-align: right;

				.gap {
					display: inline-block;
					font-size: 0;
					width: 1px;
					height: 8px;
					margin: 5px 6px 0;
					background: variables.$colorGray03;
					vertical-align: top;
				}

				.save_keyword_text {
					display: inline-block;
					color: variables.$textColorLightGray;
					@include mixins.font_size_xxs;
					vertical-align: top;

					.btn_save_keyword {
						display: inline-block;
						color: variables.$textColorGray;
						@include mixins.font_size_xxs;
						font-weight: variables.$fontWeightBold;
					}
				}
			}
		}
	}
}