@use 'sass:map';
@use '../abstracts/variables';
@use '../abstracts/functions';
@use '../abstracts/mixins';

// 메뉴, 본문 스킵 네비게이션
.skip_nav_wrap {
	position: absolute;
	font-size: 0;
	left: 0;
	right: 0;
	top: 0;
	z-index: 1000;

	a {
		position: absolute;
		display: inline-block;
		color: #fff;
		width: 100%;
		padding: 10px;
		background: #000;
		text-align: center;
		font-size: variables.$fontSizeSm;
		left: 0;
		right: 0;
		top: 0;
		box-sizing: border-box;

		&:not(:focus) {
			@include mixins.transform(translateY(-100%));
		}
	}
}

// 헤더
.wrapper {
	.header_wrapper {
		background-color: #fff;

		// 서비스 몰 이동
		.service_mall_wrap {
			position: relative;
			z-index: 101;

			&:after {
				content: '';
				position: absolute;
				right: 0;
				top: 0;
				display: block;
				width: 50vw;
				height: 100%;
				background-color: #f4f4f4;
			}

			.service_mall_inner {
				position: relative;
				padding-left: 172px;
				@include mixins.justify_width;
				@include mixins.inner_width;
				background-color: #f4f4f4;
				box-sizing: border-box;
				z-index: 2;

				&:before {
					content: '';
					position: absolute;
					top: 12px;
					left: 169px;
					width: 8px;
					height: 8px;
					border-radius: 100%;
					background-color: #3e43a4;
					z-index: 2;
				}

				&:after {
					content: '';
					position: absolute;
					top: 0;
					left: 0;
					width: 230px;
					height: 42px;
					background: url(../images/bg_service_mall_shape.png) 0 / 100% no-repeat;
					pointer-events: none;
				}

				.service_mall_box {
					flex: 1;
					padding-top: 9px;

					.service_mall_list {
						font-size: 0;

						.service_mall_item {
							position: relative;
							display: inline-block;
							vertical-align: top;

							&.brand_more {
								position: relative;
								margin-left: -7px;

								.btn_brand_more {
									padding: 6px 0 8px;
									@include mixins.font_size_xxs;
									color: variables.$textColorGray;

									&:after {
										content: '';
										position: relative;
										display: inline-block;
										width: 6px;
										height: 4px;
										margin: 6px 0 0 4px;
										background: {
											position: center;
											repeat: no-repeat;
										}
										@include mixins.ratio_images('../images/arw_service_more', 'png', 6px, 4px);
										vertical-align: top;
										@include mixins.transform(rotate(0));
										@include mixins.transition(transform .23s);
									}
								}

								.brand_more_list_box {
									display: none;
									position: absolute;
									width: 330px;
									border: 1px solid variables.$colorGray03;
									border-radius: 16px;
									background-color: #fff;
									padding: 19px;
									box-sizing: border-box;
									box-shadow: 0 5px 10px 0 rgba(variables.$textColorBlack, 0.12);

									.brand_more_list {
										font-size: 0;

										.brand_more_item {
											display: inline-block;
											width: 50%;
											box-sizing: border-box;
											vertical-align: top;

											.brand_more_link {
												&:hover, &:focus, &:active {
													.brand_name {
														text-decoration: underline;
													}
												}

												.img_box {
													display: inline-block;
													width: 34px;
													height: 34px;
													margin-right: 6px;
													border-radius: 100%;
													overflow: hidden;
													vertical-align: top;

													img {
														width: 100%;
													}
												}

												.brand_name {
													display: inline-block;
													width: calc(100% - 40px);
													margin-top: 7px;
													@include mixins.font_size_xxs;
													color: variables.$textColorGray;
													vertical-align: top;
												}
											}

											&:nth-child(odd) {
												padding-right: 9px;
											}

											&:nth-child(even) {
												padding-left: 10px;
											}

											&:nth-child(n+3) {
												margin-top: 16px;
											}
										}
									}
								}

								&.active {
									.btn_brand_more {
										&:after {
											@include mixins.transform(rotate(180deg));
										}
									}

									.brand_more_list_box {
										display: block;
										opacity: 0;
										@include mixins.transition(opacity .23s);
									}

									&.animated {
										.brand_more_list_box {
											opacity: 1;
										}
									}
								}

							}
						}
					}
				}

				.customer_service_box {
					margin: 9px 0 0 auto;

					.customer_service_list {
						font-size: 0;

						.customer_service_item {
							position: relative;
							display: inline-block;
							vertical-align: top;

							.util_link {
								display: inline-block;
								padding: 6px 0 8px;
								@include mixins.font_size_xxs;
								color: variables.$textColorGray;
								opacity: .8;
								vertical-align: top;
							}

							&.member_benefit {
								.btn_member_benefit {
									padding: 6px 0 8px;
									@include mixins.font_size_xxs;
									color: variables.$textColorGray;
									@include mixins.transition(color .23s);

									.text {
										opacity: .8;
									}

									&:after {
										position: relative;
										content: '';
										display: inline-block;
										width: 6px;
										height: 4px;
										margin: 7px 0 0 4px;
										background: {
											position: center;
											repeat: no-repeat;
										}
										@include mixins.ratio_images('../images/arw_benefit_more', 'png', 6px, 4px);
										vertical-align: top;
										@include mixins.transform(rotate(0));
										@include mixins.transition(transform .23s);
									}
								}

								.member_benefit_list {
									display: none;
									position: absolute;
									top: 28px;
									left: 0;
									width: 122px;
									padding: 13px 13px 15px;
									border: 1px solid variables.$colorGray03;
									border-radius: 10px;
									background-color: #fff;
									box-sizing: border-box;

									.member_benefit_item {
										.member_benefit_link {
											@include mixins.font_size_min;
											color: variables.$textColorGray;

											&:hover, &:focus, &:active {
												text-decoration: underline;
											}
										}

										~ .member_benefit_item {
											margin-top: 9px;
										}
									}
								}

								&.active {
									.btn_member_benefit {
										color: variables.$textColorBlack;

										&:after {
											@include mixins.transform(rotate(180deg));
										}
									}

									.member_benefit_list {
										display: block;
										opacity: 0;
										@include mixins.transition(opacity .23s);
									}

									&.animated {
										.member_benefit_list {
											opacity: 1;
										}
									}
								}
							}

							+ .customer_service_item {
								&:before {
									content: '';
									display: inline-block;
									width: 1px;
									height: 8px;
									margin: 10px 10px 0;
									background-color: variables.$textColorGray;
									opacity: .2;
									vertical-align: top;
								}
							}
						}
					}
				}
			}
		}

		.header_inner {
			position: relative;
			@include mixins.inner_width;

			.gnb_search_wrap {
				position: relative;
				padding: 31px 188px 0 206px;

				.logo_box {
					position: absolute;
					top: 9px;
					left: 0;
					display: block;
					width: 174px;
					height: 92px;

					.logo_link {
						display: block;
						height: 100%;
						background: {
							position: 0 0;
							repeat: no-repeat;
						}
						@include mixins.ratio_images('../images/img_logo_kyobo', 'png', 174px, 92px);
					}
				}

				.gnb_search_box {
					@include mixins.justify_width(center);
					width: 560px;
					height: 48px;
					padding-right: 14px;
					border-radius: 25px;
					background: #fff;
					border: 1px solid variables.$kyoboPurple;
					box-sizing: border-box;
					@include mixins.transition(width .23s variables.$transEaseBounceBack);

					.form_sel {
						align-self: flex-start;
						margin: -1px 0 0 -1px;

						.ui-selectmenu-button {
							width: 133px;
							height: 39px;
							padding: 0;
							border: 1px solid transparent;
							border-bottom: none;
							border-radius: 20px 20px 0 0;
							background: none;
							@include mixins.transition(none);

							.ui-selectmenu-text {
								padding: 12px 16px 5px 24px;
								line-height: variables.$fontSizeXsLineHeight;
								margin-right: 0;
							}

							.ui-selectmenu-icon {
								right: 16px;
								top: 21px;
								width: 6px;
								height: 4px;
								background: {
									position: center;
									repeat: no-repeat;
								}
								@include mixins.ratio_images('../images/arw_header_search', 'png', 6px, 4px);
							}

							&.ui-selectmenu-button-open {
								border-color: variables.$textColorBlack;
								background-color: #fff;

								.ui-selectmenu-text {
									font-weight: variables.$fontWeightMedium;
								}
							}
						}
					}

					.search_input_wrap {
						position: relative;
						flex: 1;
						height: 100%;
						//margin-left: 16px;
						padding-left: 16px;

						&:before {
							content: '';
							position: absolute;
							display: block;
							width: 1px;
							height: 12px;
							top: 50%;
							left: 0;
							background-color: variables.$colorGray04;
							@include mixins.transform(translateY(-50%));
						}

						.ip_gnb_search {
							width: 100%;
							height: 100%;
							background: none;
							outline: none;
							@include mixins.font_size_xs;
							@include mixins.set_placeholder_color(variables.$textColorLightGray);

							&::-webkit-search-decoration, &::-webkit-search-cancel-button, &::-webkit-search-results-button, &::-webkit-search-results-decoration {
								display: none;
								width: 0;
								height: 0;
							}

						}

						input[type=search]::-ms-clear {
							display: none;
							width: 0;
							height: 0;
						}

						input[type=search]::-ms-reveal {
							display: none;
							width: 0;
							height: 0;
						}

						.btn_ip_clear {
							display: none;
							position: absolute;
							right: 10px;
							top: 50%;
							width: 20px;
							height: 20px;
							@include mixins.transform(translateY(-50%));
							background: {
								position: center;
								repeat: no-repeat;
							}
							@include mixins.ratio_images('../images/btn_ip_reset', 'png', 18px, 18px);
						}

						&.value {
							.ip_gnb_search {
								padding-right: 40px;
							}

							.btn_ip_clear {
								display: block;
							}
						}
					}

					.btn_gnb_search {
						display: block;
						width: 30px;
						height: 30px;
						background: {
							position: center;
							repeat: no-repeat;
						}
						@include mixins.ratio_images('../images/btn_header_search', 'png', 18px, 18px);
					}
				}

				.user_menu_list {
					position: absolute;
					top: 31px;
					right: 0;
					font-size: 0;
					@include mixins.justify_width(center);

					.user_menu_item {
						position: relative;
						display: inline-block;
						vertical-align: top;

						.user_menu_link {
							display: block;
							height: 100%;

							.cnt {
								position: absolute;
								top: 0;
								right: 0;
								display: block;
								min-width: 21px;
								height: 21px;
								padding: 2px 6px 0 7px;
								border-radius: 11px;
								background-color: variables.$kyoboPurple;
								font-size: variables.$fontSizeXxs;
								font-weight: variables.$fontWeightBold;
								color: #fff;
								text-align: center;
								box-sizing: border-box;
							}
						}

						&.history {
							width: 32px;
							height: 32px;

							.user_menu_link {
								&:before {
									content: '';
									display: inline-block;
									width: 100%;
									height: 100%;
									background: {
										position: center;
										repeat: no-repeat;
									}
									@include mixins.ratio_images('../images/btn_header_history', 'png', 100%, 100%);
									vertical-align: top;
								}

								.text {
									position: absolute;
									bottom: -2px;
									left: -16px;
									font-size: 11px;
									font-weight: variables.$fontWeightBold;
									letter-spacing: variables.$letterSpacing;
									color: variables.$textColorPurple;
								}
							}
						}

						&.cart {
							width: 32px;
							height: 32px;
							margin-left: 28px;

							.user_menu_link {
								&:before {
									content: '';
									display: inline-block;
									width: 100%;
									height: 100%;
									background: {
										position: center;
										repeat: no-repeat;
									}
									@include mixins.ratio_images('../images/btn_header_cart', 'png', 100%, 100%);
									vertical-align: top;
								}

								.cnt {
									right: -9px;
									top: -5px;
								}
							}
						}

						&.my {
							width: 48px;
							height: 48px;
							margin-left: 32px;
							box-shadow: 0 5px 10px 0 rgba(77, 172, 39, 0.5);
							border-radius: 100%;

							.user_menu_link {
								overflow: hidden;
								border-radius: 100%;

								&:before {
									content: '';
									display: inline-block;
									width: 48px;
									height: 48px;
									background: {
										position: center;
										repeat: no-repeat;
									}
									@include mixins.ratio_images('../images/btn_header_my', 'png', 48px, 48px);
									vertical-align: top;
								}

								.cnt {
									display: none;
								}
							}

							&.login {
								box-shadow: 0 5px 10px 0 rgba(155, 160, 168, 0.5);

								.user_menu_link {
									&:before {
										content: none;
									}

									.img_box {
										position: absolute;
										top: 0;
										left: 0;
									}

									.cnt {
										display: block;
										top: 3px;
										right: -8px;
									}
								}

							}
						}
					}
				}

				.auto_complete_wrap {
					display: none;
					overflow: hidden;
					position: absolute;
					top: 85px;
					left: 206px;
					width: 994px;
					padding: 29px 19px 39px;
					background-color: #fff;
					border: 1px solid variables.$textColorBlack;
					border-radius: 16px;
					z-index: 2;
					box-sizing: border-box;

					.btn_layer_close {
						position: absolute;
						top: 26px;
						right: 26px;
						width: 30px;
						height: 30px;
						padding: 3px;
						box-sizing: border-box;

						.ico_layer_close {
							position: relative;
							z-index: 3;
							display: block;
							width: 24px;
							height: 24px;
							background: {
								position: center;
								repeat: no-repeat;
							}
							@include mixins.ratio_images('../images/btn_dialog_close', 'png', 24px, 24px);
						}
					}

					&.active {
						display: block;
					}
				}
			}

			.gnb_wrap {
				position: relative;
				@include mixins.justify_width(center);
				padding: 22px 0 21px;

				.anb_wrap {
					position: relative;
					margin-right: 24px;
					flex-shrink: 0;

					.btn_anb {
						display: block;
						width: 44px;
						height: 44px;
						background: {
							position: center;
							repeat: no-repeat;
						}
						@include mixins.ratio_images('../images/btn_anb_open', 'png', 44px, 44px);
						@include mixins.transition(background .23s);
					}

					.anb_area {
						display: none;
						overflow: hidden;
						border: 1px solid variables.$textColorBlack;
						border-radius: 16px;
						height: 680px;
						box-sizing: border-box;

						> .tab_wrap {
							height: 100%;

							> .tab_list_wrap {
								height: 54px;
								margin-bottom: 0;
								border-bottom: none;

								> .tabs {
									@include mixins.justify_width;
									background-color: variables.$colorGray05;

									.tab_item {
										flex: 1;
										padding: 0 30px 1px;

										.tab_link {
											&:after {
												content: none;
											}

											.tab_text {
												@include mixins.font_size_sm;
											}

											&.my_menu {
												.tab_text {
													font-weight: variables.$fontWeightBold;
												}
											}
										}
									}
								}
							}

							> .tab_content {
								height: calc(100% - 54px);

								.tab_wrap {
									&.category {
										display: inline-block;
										position: relative;
										width: calc(100% - 220px);
										padding-left: 180px;
										height: 400px;
										vertical-align: top;
										box-sizing: border-box;

										.tab_list_wrap {
											position: absolute;
											width: 130px;
											left: 0;
											top: -11px;
											height: auto;
											margin-bottom: 0;
											border-bottom: none;

											.tabs {
												.tab_item {
													width: 100%;
													padding: 0;
													border-radius: 6px;
													@include mixins.transition(background-color .23s);

													.tab_link {
														position: static;
														padding: 10px 19px 11px 16px;
														text-align: left;

														&:after {
															content: none;
														}

														.tab_text {
															@include mixins.font_size_sm;
															font-weight: variables.$fontWeightMedium;

														}
													}

													&.ui-state-active {
														background-color: variables.$kyoboPurple;

														.tab_link {
															.tab_text {
																font-weight: variables.$fontWeightBold;
																color: #fff;
															}

															&:after {
																content: '';
																position: absolute;
																top: 18px;
																left: auto;
																right: 14px;
																display: block;
																width: 5px;
																height: 8px;
																@include mixins.ratio_images('../images/arw_anb_category_tab', 'png', 5px, 8px);
																background-color: transparent;
															}
														}
													}

													~ .tab_item {
														margin-top: 10px;
													}
												}
											}
										}

										.tab_content {
											width: 100%;
											height: 100%;
										}
									}
								}

								&.anb_category {
									padding: 40px 39px 49px;

									.anb_category_inner {
										font-size: 0;

										.tab_wrap {
											&.category {
												.tab_content {
													.category_view_area {
														display: inline-block;
														width: calc(100% - 220px);
														vertical-align: top;

														.fold_box_wrap {
															&.category {
																margin-top: 17px;

																.col_area {
																	display: inline-block;
																	width: calc((100% - 100px) / 3);
																	vertical-align: top;

																	~ .col_area {
																		margin-left: 50px;
																	}
																}

																.fold_box_list {
																	font-size: 0;

																	.fold_box {
																		display: block;
																		width: 100%;
																		border: none;
																		vertical-align: top;

																		.fold_box_header {
																			@include mixins.font_size_xxs;
																			font-weight: variables.$fontWeightRegular;
																			padding: 0 10px 0 0;

																			a {
																				&:hover, &:active, &:focus {
																					text-decoration: underline;
																				}
																			}

																			.ico_new {
																				margin-left: 4px;
																			}

																			&:after {
																				width: 7px;
																				height: 7px;
																				right: 0;
																				@include mixins.ratio_images('../images/ico_fold_minus', 'png', 7px, 7px);
																			}
																		}

																		&.expanded {
																			.fold_box_header {
																				text-decoration: underline;

																				&:after {
																					@include mixins.ratio_images('../images/ico_fold_plus', 'png', 7px, 7px);
																				}
																			}

																			.fold_box_contents {
																				padding: 10px 0;

																				.category_list {
																					.category_item {
																						.category_link {
																							@include mixins.font_size_xxs;
																							color: variables.$textColorLightGray;

																							&:hover, &:active, &:focus {
																								text-decoration: underline;
																							}
																						}

																						~ .category_item {
																							margin-top: 6px;
																						}
																					}
																				}
																			}
																		}

																		&.gap {
																			margin-top: 24px;
																		}

																		~ .fold_box {
																			margin-top: 10px;

																			&.gap {
																				margin-top: 24px;
																			}
																		}
																	}
																}
															}
														}
													}

													.special_corner_area {
														display: inline-block;
														width: 120px;
														margin: 0 40px 0 60px;
														vertical-align: top;

														.title_wrap {
															padding-bottom: 17px;
														}

														.special_corner_list {
															.special_corner_item {
																.special_corner_link {
																	@include mixins.font_size_xxs;
																	color: variables.$textColorGray;
																}

																~ .special_corner_item {
																	margin-top: 10px;
																}
															}
														}
													}
												}
											}
										}

										.banner_box {
											display: inline-block;
											width: 200px;
											margin-left: 20px;
											vertical-align: top;
											border-radius: 16px;
											overflow: hidden;

											.banner_link {
												display: block;
											}

											img {
												width: 100%;
											}
										}
									}

									.recommend_service_list {
										margin-top: 40px;
										text-align: right;
										font-size: 0;

										.recommend_service_item {
											display: inline-block;
											vertical-align: top;

											.recommend_service_link {
												display: block;

												.img_box {
													display: block;
													width: 70px;
													height: 70px;
													border-radius: 16px;
													overflow: hidden;

													img {
														width: 100%;
													}

													&.no_img {
														@include mixins.ratio_images('../images/img_anb_recommend_service_noimg', 'png', 70px, 70px);

														img {
															display: none;
														}
													}
												}

												.title {
													display: block;
													margin-top: 6px;
													@include mixins.font_size_xxs;
													color: variables.$textColorLightGray;
													text-align: center;
												}
											}

											~ .recommend_service_item {
												margin-left: 37px;
											}
										}
									}

								}

								&.anb_service {
									padding: 40px 45px 50px;

									.all_service_list {
										width: calc(100% + 50px);
										margin: 0 -25px;
										font-size: 0;

										.all_service_item {
											float: left;
											width: calc(100% / 6);
											margin-bottom: 50px;
											padding: 0 25px;
											box-sizing: border-box;

											&:after {
												@include mixins.clearfix;
											}

											&:nth-last-child(-n+6) {
												margin-bottom: 0;
											}

											.title_wrap {
												min-height: auto;
												padding-bottom: 17px;

												.title_link {
													&:hover, &:active, &:focus {
														text-decoration: underline;
													}
												}
											}

											.btn_arw_link {
												margin-bottom: 17px;
											}

											.sub_depth_list {
												.depth_item {
													.depth_link {
														@include mixins.font_size_sm;
														color: variables.$textColorGray;

														&:hover, &:active, &:focus {
															text-decoration: underline;
														}
													}

													~ .depth_item {
														margin-top: 10px;
													}
												}
											}

											&:nth-child(6n) {
												margin-right: 0;
											}
										}
									}
								}

								&.anb_mymenu {
									padding: 40px 49px 49px 39px;

									.tab_wrap {
										&.category {
											width: 100%;
											height: 100%;

											.tab_content {
												font-size: 0;

												.category_inner {
													display: inline-block;
													width: calc(100% - 390px);
													height: 100%;
													vertical-align: top;

													.custom_scroll_wrap {
														height: 100%;
													}
												}

												.title_wrap {
													padding-bottom: 17px;
													min-height: auto;
												}

												.category_group {
													font-size: 0;
													margin: 0 -30px;

													.col_area {
														display: inline-block;
														width: calc(100% / 3);
														padding: 0 30px;
														vertical-align: top;
														box-sizing: border-box;
													}
												}

												.category_list {
													font-size: 0;

													.category_item {
														.category_name {
															@include mixins.font_size_xxs;
														}

														+ .category_item {
															margin-top: 20px;
														}
													}
												}

												.favorite_menu_wrap {
													display: inline-block;
													width: 320px;
													margin-left: 70px;
													vertical-align: top;

													.btn_wrap {
														margin-bottom: 8px;
														text-align: right;
													}

													.custom_scroll_wrap {
														height: 497px;
													}

													.favorite_menu_list {
														.favorite_menu_item {
															padding: 16px 15px 14px;
															border: 1px solid variables.$colorGray04;
															border-radius: 6px;

															.favorite_menu_link {
																font-size: 0;

																.depth01_title {
																	font-weight: variables.$fontWeightBold;
																}

																.gap {
																	display: inline-block;
																	width: 5px;
																	height: 8px;
																	margin: 5px 4px 0 6px;
																	@include mixins.ratio_images('../images/arw_favorite_depth', 'png', 5px, 8px);
																	vertical-align: top;
																}

																.depth01_title, .depth02_title {
																	@include mixins.font_size_xxs;
																}
															}

															~ .favorite_menu_item {
																margin-top: 8px;
															}
														}
													}

													&.no_data {
														.no_data {
															margin: 0;
															padding: 0;
															@include mixins.justify_width(center, center, column);
															height: 497px;
															border: 1px solid variables.$colorGray04;
															border-radius: 6px;
															box-sizing: border-box;
														}
													}
												}
											}
										}
									}
								}
							}
						}
					}

					&.active {
						.btn_anb {
							@include mixins.ratio_images('../images/btn_anb_open_active', 'png', 44px, 44px);
						}

						.anb_area {
							position: absolute;
							display: block;
							opacity: 0;
							left: 0;
							top: 61px;
							width: variables.$defaultInnerWidth;
							background-color: #fff;
							z-index: 100;
							@include mixins.transition(opacity .23s);
						}

						&.animated {
							.anb_area {
								opacity: 1;
							}
						}

						@at-root {
							.header_wrapper {
								&.sps-blw {
									z-index: 100;

									.header_inner {
										.gnb_wrap {
											.anb_wrap {
												&.active {
													.anb_area {
														top: 61px;
													}
												}
											}
										}
									}
								}
							}
						}
					}
				}

				.gnb_list {
					font-size: 0;

					.gnb_item {
						display: inline-block;
						vertical-align: top;

						.gnb_link {
							display: inline-block;
							@include mixins.font_size_def;
							font-weight: variables.$fontWeightBold;
							vertical-align: top;

							&.picks {
								img {
									display: block;
									@include mixins.transform(translateY(-2px));
								}
							}

							.ico_new {
								margin-left: 4px;
							}
						}

						+ .gnb_item {
							&:before {
								content: '';
								display: inline-block;
								width: 2px;
								height: 2px;
								margin: 10px 14px 0;
								background-color: variables.$textColorBlack;
								vertical-align: top;
							}
						}
					}
				}

				.gnb_sub_list {
					margin-left: auto;
					font-size: 0;

					.gnb_sub_item {
						display: inline-block;
						vertical-align: top;

						.gnb_sub_link {
							@include mixins.font_size_xs;
							color: variables.$textColorGray;
						}

						&.more_service {
							position: relative;
							margin-left: 9px;

							.btn_more_service {
								display: block;
								width: 20px;
								height: 20px;
								@include mixins.ratio_images('../images/btn_gnb_service_more', 'png', 20px, 20px);
								@include mixins.transition(background .23s);
							}

							.more_service_list {
								display: none;
								position: absolute;
								top: 26px;
								right: 0;
								width: 132px;
								padding: 13px;
								background-color: #fff;
								border-radius: 10px;
								border: 1px solid variables.$colorGray03;
								box-sizing: border-box;
								@include mixins.transition(opacity .23s);

								.more_service_item {
									.more_service_link {
										@include mixins.font_size_xxs;
										color: variables.$textColorGray;

										img {
											width: 100%;
										}
									}

									~ .more_service_item {
										margin-top: 8px;
									}
								}
							}

							&.active {
								.btn_more_service {
									@include mixins.ratio_images('../images/btn_gnb_service_more_active', 'png', 20px, 20px);
								}

								.more_service_list {
									display: block;
									opacity: 0;
									z-index: 100;
								}

								&.animated {
									.more_service_list {
										opacity: 1;
									}
								}
							}
						}

						+ .gnb_sub_item {
							&:before {
								content: '';
								display: inline-block;
								width: 2px;
								height: 2px;
								margin: 9px 9px 0;
								background-color: variables.$textColorGray;
								vertical-align: top;
							}

							&.more_service {
								&:before {
									content: none;
								}
							}
						}
					}
				}

				.curtain_banner_wrap {
					position: relative;
					height: 87px;
					margin: -22px 0 -21px;
					@include mixins.justify_width(center);

					&:before {
						content: '';
						display: inline-block;
						width: 4px;
						height: 8px;
						margin: 0 16px;
						vertical-align: top;
						@include mixins.ratio_images('../images/bg_curtain_gap', 'png', 4px, 8px);
					}

					.btn_curtain_expand {
						display: inline-block;
						vertical-align: top;

						.img_box {
							position: relative;
							display: block;
							width: 95px;
						}
					}

					.curtain_banner_box {
						overflow: hidden;
						display: none;
						position: absolute;
						top: 76px;
						right: 0;
						z-index: 10;
						border-radius: 20px;

						.btn_curtain_close {
							position: absolute;
							top: 25px;
							right: 27px;
							width: 30px;
							height: 30px;
							background: {
								position: center;
								repeat: no-repeat;
							}
							@include mixins.ratio_images('../images/btn_curtain_close', 'png', 24px, 24px);
						}
					}

					&.active {
						.curtain_banner_box {
							display: block;
							opacity: 0;
							@include mixins.transition(opacity .23s);
						}

						&.animated {
							.curtain_banner_box {
								opacity: 1;
							}
						}
					}
				}
			}
		}

		&.sps {
			&.sps-blw {
				position: fixed;
				left: 0;
				top: 0;
				width: 100%;
				min-width: variables.$defaultMinWidth;
				z-index: 100;
				border-bottom: 1px solid variables.$colorGray04;

				.service_mall_wrap, .curtain_banner_wrap {
					display: none;
				}

				.gnb_search_wrap {
					position: absolute;
					top: 16px;
					left: 68px;
					padding: 0;
					@include mixins.justify_width(center);
					z-index: 2;
					width: calc(100% - 68px);

					.logo_box {
						position: static;
						width: 118px;
						height: 46px;
						margin-right: 20px;

						.logo_link {
							@include mixins.ratio_images('../images/img_logo_kyobo_sm', 'png', 118px, 46px);
						}
					}

					.gnb_search_box {
						width: 500px;
						height: 44px;

						.form_sel {
							.ui-selectmenu-button {
								height: 36px;

								.ui-selectmenu-icon {
									top: 18px;
								}
							}

							.ui-selectmenu-text {
								padding: 9px 16px 5px 24px
							}
						}
					}

					.user_menu_list {
						position: static;
						margin-left: auto;
					}

					.auto_complete_wrap {
						top: 51px;
						left: 138px;
					}
				}

				.gnb_wrap {
					padding: 18px 472px 17px 0;

					.gnb_list {
						display: none;
					}

					.gnb_sub_list {
						display: none;
					}
				}

				~ .container_wrapper {
					padding-top: 208px;
				}
			}
		}
	}
}
